/* div에 스타일 적용 */
.selectDiv {
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 10px; /* 내용과 외곽선 사이 간격 */
    background-color: #f2f2f2; /* 배경색 설정 */
    width: 50%;
    max-width: 100%;
}

/* table에 스타일 적용 */
.playerTable {
    border-collapse: collapse; /* 테이블 셀 경계 통합 */
    border: 1px solid #ccc; /* 회색 외곽선 */
    width: 100%; /* 테이블 너비를 100%로 설정 */
    max-width: 100%; /* 최대 너비를 100%로 설정 */
    margin-bottom: 20px; /* 아래 여백 추가 */
}

/* 테이블 행과 열에 외곽선 추가 */
.playerTable td, .playerTable th {
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 10px; /* 셀 내용과 외곽선 사이 간격 */
    text-align: center; /* 텍스트 중앙 정렬 */
}

/* 테이블 헤더에 배경색과 글자 색상 설정 */
.playerTable th {
    background-color: #f2f2f2; /* 배경색 설정 */
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 10px; /* 셀 내용과 외곽선 사이 간격 */
    text-align: center; /* 텍스트 중앙 정렬 */
}

/* 등번호와 이름 사이의 간격을 조절 */
.playerTable td:nth-child(2) {
    padding-right: 30px;
}

/* 불필요한 셀 내용 여백 제거 */
.playerTable td:first-child, .playerTable th:first-child {
    padding-left: 0;
}